import React, {useEffect, useRef, useState} from 'react';
import Breadcrumbs from "../../reusable/Layout/Breadcrumbs";
import {useDispatch, useSelector} from "react-redux";
import {Link, useParams} from "react-router-dom";
import Spinner from "../../reusable/Loader/Spinner";
import {metaTitle} from "../../../utils/misc";
import NotFound404 from "../404";
import {fetchWooCommerceProductData} from "../../../redux/thunks/fetchWooCommerceProductData";
import Boolean from "../../reusable/Boolean";
import WooCommerceProductDataVisibility from "../../reusable/WooCommerceProductDataVisibility";
import Layout from "../../reusable/Layout";
import ActionsBar from "../../reusable/Layout/ActionsBar";
import useTranslation from "../../../hooks/useTranslation";

const ViewWooCommerceProductData = () => {

    // manage global state
    const {fetched, loading} = useSelector(state => state.fetchWooCommerceProductDataReducer);
    const dispatch = useDispatch();

    // manage local state
    const {id} = useParams();
    const didMountRef = useRef(false);
    const [fetchedSuccess, setFetchedSuccess] = useState(null);

    useEffect(() => {
        dispatch(fetchWooCommerceProductData({
            id:id
        }));
        metaTitle(useTranslation("View WooCommerce product data"));
    }, [id]);

    // handle fetch outcome
    useEffect(() => {
        if (didMountRef.current){
            if(!loading){
                if(fetched.length !== 0){
                    setFetchedSuccess(true);
                } else {
                    setFetchedSuccess(false);
                }
            }
        } else {
            didMountRef.current = true;
        }
    }, [loading]);

    if(fetchedSuccess === null){
        return <Spinner/>;
    }

    if(!fetchedSuccess){
        return <NotFound404/>;
    }

    const actions = (
        <React.Fragment>
            <Link
                className="acpt-btn acpt-btn-primary"
                to={`/product-data/product/edit/${id}`}>
                {useTranslation("Edit")}
            </Link>
            <Link
                className="acpt-btn acpt-btn-primary-o"
                to={`/product-data/product/fields/${id}`}>
                {useTranslation("Manage fields")}
            </Link>
        </React.Fragment>
    );

    return (
        <Layout>
            <ActionsBar
                title={`${fetched[0].name}: ${useTranslation("details")}`}
                actions={actions}
            />
            <main>
                <Breadcrumbs crumbs={[
                    {
                        label: useTranslation("Registered Custom Post Types"),
                        link: "/"
                    },
                    {
                        label: useTranslation("WooCommerce product data"),
                        link: "/product-data/product"
                    },
                    {
                        label: useTranslation("View WooCommerce product data")
                    }
                ]} />
                <div className="acpt-card">
                    <div className="acpt-card__inner">
                        <table className={`acpt-table acpt-table-secondary ${document.globals.is_rtl === true ? `rtl` : ``}`}>
                            <tr>
                                <th style={{ width: "180px" }}>
                                    {useTranslation("Name")}
                                </th>
                                <td>
                                    {fetched[0].name}
                                </td>
                            </tr>
                            <tr>
                                <th style={{ width: "180px" }}>
                                    {useTranslation("Icon")}
                                </th>
                                <td>
                                    <span className={`wcicon-${fetched[0].icon.icon}`} />
                                </td>
                            </tr>
                            <tr>
                                <th style={{ width: "180px" }}>
                                    {useTranslation("Visibility")}
                                </th>
                                <td>
                                    <WooCommerceProductDataVisibility visibility={fetched[0].visibility} />
                                </td>
                            </tr>
                            <tr>
                                <th style={{ width: "180px" }}>
                                    {useTranslation("Show in UI")}
                                </th>
                                <td>
                                    <Boolean status={fetched[0].showInUI}/>
                                </td>
                            </tr>
                        </table>
                    </div>
                </div>
            </main>
        </Layout>
    );
};

export default ViewWooCommerceProductData;